import styled from 'styled-components';
import logoPic from '../../statics/nav-logo-4c7bbafe27adc892f3046e6978459bac.png';

export const HeaderWrapper = styled.div`
	z-index: 1;
	position: relative;
	height: 58px;
	border-bottom: 1px solid #f0f0f0;
`;
export const Logo = styled.div`
	position: absolute;
	top: 6px;
	left: 0;
	display: block;
	width: 100px;
	height: 59px;
	background: url(${logoPic});
	background-size: contain;
`;
export const Nav = styled.div`
	width: 960px;
	height: 100%;
	padding-right: 70px;
	box-sizing: border-box;
	margin: 0 auto;
`;
export const NavItem = styled.div`
	line-height: 56px;
	font-size: 17px;
	padding: 0 15px;
	color: #333;
	&.left {
		float: left;
	}
	&.right {
		float: right;
		color: #969696;
	}
	&.active {
		color: #ea6f5a;
	}
`;
export const NavSearch = styled.input.attrs({
	placeholder: '搜索',
})`
	width: 160px;
	height: 38px;
	padding: 0 30px 0 20px;
	box-sizing: border-box;
	border: none;
	outline: none;
	border-radius: 19px;
	margin-top: 9px;
	background: #eee;
	font-size: 14px;
	margin-left: 20px;
	color: #777;
	&::placeholder {
		color: #999;
	}
	&.focused {
		width: 240px;
	}
	&.slide-enter {
		transition: all 0.2s ease-out;
	}
	&.slide-enter-avtive {
		width: 240px;
	}
	&.slide-exit {
		transition: all 0.2s ease-out;
	}
	&.slide-exit-active {
		width: 160px;
	}
`;
export const SearchWrapper = styled.div`
	float: left;
	position: relative;
	.iconfont {
		position: absolute;
		right: 5px;
		bottom: 5px;
		width: 30px;
		line-height: 30px;
		border-radius: 15px;
		text-align: center;
		&.focused {
			background: #777;
			color: #fff;
		}
	}
`;
export const SearchInfo = styled.div`
	position: absolute;
	left: 0;
	top: 56px;
	width: 240px;
	padding: 0 20px;
	background: #fff;
	box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
`;
export const SearchInfoTitle = styled.div`
	margin-top: 20px;
	margin-bottom: 15px;
`;
export const SearchInfoList = styled.div``;
export const SearchInfoSwitch = styled.span`
	float: right;
	font-size: 13px;
`;
export const SearchInfoItem = styled.a`
	display: block;
	float: left;
	font-size: 12px;
	padding: 0 5px;
	color: #787878;
	border: 1px solid #ddd;
	line-height: 20px;
	margin-right: 10px;
	margin-bottom: 15px;
	border-radius: 3px;
`;
export const Addition = styled.div`
	position: absolute;
	right: 0;
	top: 0;
	height: 56px;
`;
export const Button = styled.div`
	float: right;
	margin-top: 9px;
	line-height: 38px;
	border-radius: 19px;
	border: 1px solid #ec6149;
	margin-right: 20px;
	padding: 0 20px;
	font-size: 14px;
	&.reg {
		color: #ec6149;
	}
	&.writting {
		color: #fff;
		background: #ec6149;
	}
`;
